import uniPopup from "@/components/uni-popup/uni-popup.vue"
import SubmitButton from '../../components/SubmitButton/SubmitButton.vue'
export default {
	data () {
		return {
			payInfo: [{
				image: '../../static/image/my/weixinzhifu.png',
				text: '微信支付',
				sectext: '',
			}], // 支付方式
			circleShow: 0 ,// 支付圆圈
			myEduMoney: 0 ,// 学币余额,
			rechargePrice: '',
			backFlag: 0
		}
	},
	onLoad(options) {
		if (options.rechargeIndex) {
			this.backFlag = options.rechargeIndex  // 判断要怎么样回到上一页 
		}
		this.getUserInfo()
	},
	components: {
		SubmitButton,
		uniPopup
	},
	methods: {
		// 个人信息
		async getUserInfo(){
			
			let {data: res} = await this.$fetch(this.$api.userInfo,{},'GET', 'form')
			this.myEduMoney = res.money
		},
		// 显示底部模态框
		submitClick () {
			if (this.rechargePrice.trim() == '') {
				return uni.showToast({
					icon: 'none',
					title: '请先填写充值金额'
				})
			}
			this.$refs.popupPay.open()
		},
		// 更改支付方式
		handleCircle (index) {
			this.circleShow = index
		},
		// 修正金额
		checkPrice () {
			console.log(this.rechargePrice)
			this.rechargePrice = Number(this.rechargePrice).toFixed(2)
		},
		// 支付
		async payOrder () {
			let openId = uni.getStorageSync('openId')
			let res = await this.$fetch(this.$api.xcxPay, {totalAmount: this.rechargePrice,openId}, 'POST', 'FORM')
			console.log(res)
			let payInfo = JSON.parse(res.msg)
			console.log(payInfo)
		
			uni.requestPayment({
			    provider: 'wxpay',
			    timeStamp: payInfo.timeStamp,
			    nonceStr: payInfo.nonceStr,
			    package: payInfo.package,
			    signType: payInfo.signType,
			    paySign: payInfo.paySign,
			    success: (res) => {
					this.$refs.popupPay.close()
					uni.showToast({
						icon: 'success',
						title: '支付成功'
					})
					setTimeout(() => {
						
						if (this.backFlag) {
							uni.switchTab({
								url:'../my/my'
							})
						} else {
							uni.navigateBack({
								delta: 1
							})
						}
						
					},1000)
			        console.log('success:' + JSON.stringify(res));
			    },
			    fail: (err) => {
					this.$refs.popupPay.close()
					uni.showToast({
						icon: 'success',
						title: '支付失败'
					})
			        console.log('fail:' + JSON.stringify(err));
			    }
			});
		}
	}
}
